<script>
	import Router from "svelte-spa-router";
	import { currentUser } from "./stores";
	import { addingStatus } from "./stores";
	import { push } from "svelte-spa-router";
	import Home from "./routes/Home.svelte";
	import About from "./routes/About.svelte";
	import Login from "./routes/Login.svelte";
	import AddUser from "./routes/AddUser.svelte";
	import AddProgram from "./routes/AddProgram.svelte";
	import ModProfessional from "./routes/ModProfessional.svelte";
	import ModUser from "./routes/ModUser.svelte";
	import { loadingCircle } from "./stores";
	let routes = {
		"/" : Home,
		"/about" : About,
		"/login": Login,
		"/add-user" : AddUser,
		"/add-program": AddProgram,
		"/mod-professional": ModProfessional,
		"/mod-user": ModUser
	};

	if(!$currentUser.loggedIn){
		push('/login');
	}

	const unsubscribe = currentUser.subscribe(value => {
  	//console.log('Value changed:', value);
  	if(value.loggedIn === true){
		const unsubscribeAdder = addingStatus.subscribe(value => {
		//console.log('Value changed:', value);
		if(value == 1){
			push('/add-user');
		} else if(value == 2){
			push('/add-program');
		} else if(value == 3){
			push('/mod-professional');
		} else if (value == 4){
			push('/mod-user');
		}
		else{
			push('/');
		}
		});
	}else{
		push('/login');
	}
	});

	



	// To unsubscribe from the store's changes:
	// unsubscribe();
</script>

<main>
	<Router {routes}/>
	{#if $loadingCircle}
	<div id="loadingPopup" class="popup">
		<div class="loading-circle">
		  <i class="fa-solid fa-spinner fa-spin"></i>
		</div>
	  </div>
	{/if}
	
</main>

