<script>
  import {currentUser} from '../stores.js'
  import { logout } from '../loginService.js';
  import { currentProfile } from '../stores.js';
  
  import Admin from '../roles/Admin.svelte';
  import Professional from '../roles/Professional.svelte';
  import User from '../roles/User.svelte';
  import { onMount } from 'svelte';

  onMount(() => {
    const script = document.createElement('script');
    script.src = 'https://kit.fontawesome.com/6419d82e9f.js'; // Replace with your Font Awesome kit URL
    script.async = true;
    document.head.appendChild(script);
  });


  function onLogout() {
    
    logout();
  }

</script>


<main>
  <div class="container">
    <header class="header">
      <div class="logo-container">
        <img src="inhealion.png" alt="Inhealion Logo" class="logo-small" />
        <h2 class="company-name">Inhealion</h2>
      </div>
      <div class = "status-panel">
      <div class="user-icon">
        {#if $currentUser.user_role === "Administrator"}
          <i class="fa-solid fa-user-shield"></i>
        {:else if $currentUser.user_role === "Professional" || $currentUser.user_role === "S. Professional"}
          <i class="fa-solid fa-user-tie"></i>
        {:else}
          <i class="fa-solid fa-user"></i>
        {/if}
      </div>
      <div class="user-details">
        <h3 class="username">{$currentProfile.name} {$currentProfile.surname}</h3>    
        <p class="user-role">{$currentUser.user_role}</p>                  
      </div>
      <div class = "logout-section">
        <button class="logout-button" on:click={onLogout}>
          <i class="fa-solid fa-sharp fa-arrow-right-from-bracket"></i>
        </button>
      </div>
    </div>
    </header> 
    {#if $currentUser.loggedIn}
      <div class = "subcontainer">
        {#if $currentUser.user_role === "Administrator"}
        <Admin />
        {:else if $currentUser.user_role === "Professional" || $currentUser.user_role === "S. Professional"}
          <Professional />
        {:else}
          <User />
        {/if}
      </div>
      
    {/if}
</div>  
</main>




  <style>
    main {
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: 100vh;
    min-width: 100vw;
    margin: 0;
  }
    
    .container {
      height: 100vh;
      min-width: 100vw;
      margin: 0;
      background-color: #fff;
      padding: 0px; /* Remove padding around the container */
      display: flex;
      flex-direction: column;
    
  }

  .subcontainer {
    flex-grow: 1;
    max-height: 90vh;
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: #efeeee;
    height: 10vh;
    position: sticky;
    top: 0px;
    
  }

  .status-panel{
    display: flex;
    flex-direction: row;
  }

  .user-icon {
    display: flex;
    align-items: center;
    margin: 2px 10px;
    font-size: 4vh;
  }

  .user-details {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    margin: 2px 10px;
    font-size: 4vh;
  }

  .logout-section {
    display: flex;
    align-items: center;
  }

  .logo-container {
    display: flex;
    align-items: center;
    padding: 10px;
    margin-left: 10px;
  }

  .logo-small {
    width: 6.5vh;
    height: 6.5vh;
  }

  .company-name {
    color: #00b6a0;
    font-size: 3.5vh;
    margin-left: 10px;
    font-weight: bold;
  }

  .logout-button {
    padding: 8px;
    margin: 2px;
    margin-right: 10px;
    background-color: transparent;
    color: #00b6a0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: lighter;
    font-size: 3vh;
    transition: background-color 0.3s ease;
  }

  .logout-button:hover {
    color: #a42e2e;
    background-color: #f4eeee;
  }

  .user-role {
    font-size: 1.8vh;
    color: #555;
    margin: 3px 0px;
  }
  .username {
    font-size: 2.1vh;
    margin-bottom: 2px;
    margin-top: 2px;
  }

  @media (max-height: 768px) {
    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      background-color: #efeeee;
      
    }
  
    .status-panel{
      display: flex;
      flex-direction: row;
    }
  
    .user-icon {
      display: flex;
      align-items: center;
      margin: 2px 10px;
      font-size: 24px;
    }
  
    .user-details {
      display: flex;
      flex-direction: column;
      align-items: start;
      justify-content: center;
      margin: 2px 10px;
      font-size: 24px;
    }
  
    .logout-section {
      display: flex;
      align-items: center;
    }
  
    .logo-container {
      display: flex;
      align-items: center;
      padding: 10px;
      margin-left: 10px;
    }
  
    .logo-small {
      width: 40px;
      height: 40px;
    }
  
    .company-name {
      color: #00b6a0;
      font-size: 20px;
      margin-left: 10px;
      font-weight: bold;
    }
  
    .logout-button {
      padding: 8px;
      margin: 2px;
      margin-right: 10px;
      background-color: transparent;
      color: #00b6a0;
      border: none;
      border-radius: 4px;
      cursor: pointer;
      font-weight: lighter;
      font-size: 18px;
      transition: background-color 0.3s ease;
    }
  
    .logout-button:hover {
      color: #a42e2e;
      background-color: #f4eeee;
    }
  
    .user-role {
      font-size: 11px;
      color: #555;
      margin: 3px 0px;
    }
    .username {
      font-size: 13px;
      margin-bottom: 2px;
      margin-top: 2px;
    }
  
  
  
}
 
  @media (max-width: 768px) {
        .container {
            max-width: 400px;
        }
    }

  

  </style>


